import React from 'react';
import { Easing } from '../utils/easing';

function input(type, preset, key, onChange, options = {}) {
  return (
    <tr>
      <td style={{ width: 108 }}>
        <strong>
          <label htmlFor={key}>{key}</label>
        </strong>
      </td>
      <td>
        <input
          id={key}
          type={type}
          value={preset[key]}
          checked={type === 'checkbox' ? preset[key] : false}
          onChange={e =>
            onChange({
              [key]:
                type === 'checkbox'
                  ? e.target.checked
                  : type === 'number'
                  ? parseFloat(e.target.value)
                  : e.target.value,
            })
          }
          {...options}
        />
      </td>
    </tr>
  );
}

function select(preset, key, options, onChange) {
  return (
    <tr>
      <td>
        <strong>
          <label htmlFor={key}>{key}</label>
        </strong>
      </td>
      <td>
        <select
          id={key}
          value={preset[key]}
          onChange={e => onChange({ [key]: e.target.value })}
        >
          {' '}
          {options.map(option => (
            <option key={option} value={option}>
              {option}
            </option>
          ))}
        </select>
      </td>
    </tr>
  );
}

//////////////////////////////////////////////////

export default function Inputs({ preset, onChange }) {
  return (
    <table className="inputs">
      <tbody className="inputs-body">
        {input('text', preset, 'name', onChange)}
        {input('number', preset, 'seed', onChange, { min: 0, step: 1 })}
        {input('number', preset, 'stars', onChange, { min: 0, step: 5000 })}

        <tr>
          <td>
            <h4>Shape</h4>
          </td>
        </tr>
        {select(preset, 'type', ['spiral', 'globular'], onChange)}
        {select(preset, 'density', Object.keys(Easing), onChange)}
        {input('number', preset, 'spread', onChange, { min: 0, step: 1 })}
        {select(preset, 'spreadShape', Object.keys(Easing), onChange)}
        {select(preset, 'sideShape', Object.keys(Easing), onChange)}
        {input('number', preset, 'height', onChange, { min: 0, step: 1 })}
        {input('number', preset, 'heightSpread', onChange, {
          min: 0,
          max: 1,
          step: 0.1,
        })}

        {preset.type === 'spiral' && (
          <>
            {input('number', preset, 'arms', onChange, { min: 1, step: 1 })}
            {input('number', preset, 'armsCurve', onChange, {
              min: 0.25,
              step: 0.01,
            })}
          </>
        )}

        <tr>
          <td>
            <h4>Colors</h4>
          </td>
        </tr>
        {input('color', preset, 'spaceColor', onChange)}
        {input('color', preset, 'coreColor', onChange)}
        {input('color', preset, 'starColor0', onChange)}
        {input('color', preset, 'starColor1', onChange)}
        {input('color', preset, 'starColor2', onChange)}
        {input('color', preset, 'starColor3', onChange)}
        {input('color', preset, 'starColor4', onChange)}

        <tr>
          <td>
            <h4>Sizes</h4>
          </td>
        </tr>
        {input('number', preset, 'minStarSize', onChange, {
          min: 0,
          step: 0.1,
        })}
        {input('number', preset, 'maxStarSize', onChange, {
          min: 0,
          step: 0.1,
        })}

        <tr>
          <td>
            <h4>Camera</h4>
          </td>
        </tr>
        {input('number', preset, 'cameraX', onChange, { step: 0.05 })}
        {input('number', preset, 'cameraY', onChange, { step: 0.05 })}
        {input('number', preset, 'cameraZ', onChange, { step: 0.05 })}

        <tr>
          <td>
            <h4>Effects</h4>
          </td>
        </tr>
        {input('number', preset, 'backgroundGlow', onChange, {
          min: 0,
          step: 0.1,
        })}
        {input('number', preset, 'starGlow', onChange, {
          min: 0,
          max: 10,
          step: 0.1,
        })}
        {input('number', preset, 'coreSize', onChange, { min: 0, step: 1 })}
        {input('number', preset, 'coreBlur', onChange, { min: 0, step: 1 })}

        <tr>
          <td>
            <h4>Animation</h4>
          </td>
        </tr>
        {input('checkbox', preset, 'animated', onChange)}
        {input('number', preset, 'speed', onChange, {
          min: 0.1,
          step: 0.1,
        })}
      </tbody>
    </table>
  );
}
